.App {
  text-align: center;
  font-family: Arial, sans-serif;
}

.search-form {
  border: 5px solid rgb(136, 132, 216);
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  padding: 20px;
}

.search-form * {
  font-size: 20px;
  box-sizing: border-box;
  outline: none;
  margin: 0 auto;
}

.search-form__input {
  border: 1px solid rgb(136, 132, 216);
  width: 60%;
  height: 60px;
  border-radius: 10px;
  margin-bottom: 20px;
  padding: 10px;
}

.search-form__button {
  background: rgba(136, 132, 216, 0.3);
  border: 1px solid rgb(136, 132, 216);
  box-shadow: none;
  border-radius: 10px;
  padding: 10px;
  transition: background-color 0.5s;
  margin-bottom: 10px;
}

.search-form__button:hover {
  cursor: pointer;
  background: rgba(136, 132, 216, 0.6);
}

.search-form__button:disabled:hover {
  cursor: auto;
  background: rgba(136, 132, 216, 0.3);
}

.search-form__error-text {
  color: red;
  font-size: 14px;
}
